  background-image: url("../../img/map/map-desktop.jpg");
  background-repeat: no-repeat;
  background-size: 585px auto;
  background-position: top center;
  background-color: #04723b;
}
@media (max-width: 1199px) {
  .map__id {
    height: 340px;
    width: 100%;
    background-image: url("../../img/map/map-tablet.jpg");
    background-repeat: no-repeat;
    background-size: 678px auto;
    background-position: top center;
    background-color: #04723b;
  }
}
@media (max-width: 765px) {
  .map__id {
    height: 320px;
    width: 100%;
    background-image: url("../../img/map/map-mobile.jpg");
    background-repeat: no-repeat;
    background-size: 290px auto;
    background-position: top center;
    background-color: #04723b;
  }
}

.map__id:focus-visible {
  outline: none;
}

.footer {
  background-color: #ffffff;
  color: #2d383f;
}

.footer__container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 30px 120px;
}
@media (max-width: 1439px) {
  .footer__container {
    padding: 44px 50px 40px 45px;
  }
}
@media (max-width: 1199px) {
  .footer__container {
    padding: 44px 50px 39px 45px;
  }
}
@media (max-width: 765px) {
  .footer__container {
    padding: 30px 20px;
  }
}

.footer__navbar {
  display: grid;
  grid-template-columns: 205px 1fr 142px;
  margin-bottom: 15px;
  gap: 20px;
}
@media (max-width: 1199px) {
  .footer__navbar {
    display: grid;
    grid-template-columns: 118px 1fr;
    margin-bottom: 37px;
  }
}
@media (max-width: 765px) {
  .footer__navbar {
    display: block;
    margin-bottom: 5px;
  }
}

.footer__logo {
  width: 205px;
  height: 62px;
}
@media (max-width: 1199px) {
  .footer__logo {
    width: 120px;
    height: 34px;
  }
}
@media (max-width: 765px) {
  .footer__logo {
    width: 112px;
    height: 34px;
    margin-bottom: 13px;
  }
}

@media (max-width: 1199px) {
  .footer__logo-desktop {
    display: none;
  }
}
@media (max-width: 765px) {
  .footer__logo-desktop {
    display: none;
  }
}

.footer__logo-tablet {
  display: none;
}
@media (max-width: 1199px) {
  .footer__logo-tablet {
    display: block;
  }
}
@media (max-width: 765px) {
  .footer__logo-tablet {
    display: none;
  }
}

.footer__logo-mobile {
  display: none;
}
@media (max-width: 765px) {
  .footer__logo-mobile {
    display: block;
  }
}

.footer__list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 33px;
  margin: auto;
  padding: 0;
  padding-right: 60px;
  padding-top: 5px;
  color: inherit;
  font-size: 16px;
  line-height: 100%;
}
@media (max-width: 1199px) {
  .footer__list {
    margin-right: 0;
    font-size: 14px;
    padding-right: 0;
    gap: 21px;
    padding-bottom: 10px;
  }
}
@media (max-width: 765px) {
  .footer__list {
    display: block;
    column-count: 2;
    column-gap: 33px;
  }
}

.footer__item {
  border-bottom: solid 1px #ffffff;
}
.footer__item:hover {
  border-bottom: solid 1px #2d383f;
}
.footer__item:active {
  border-bottom: solid 1px #ffffff;
}
@media (max-width: 765px) {
  .footer__item {
    margin-bottom: 14px;
  }
}

.footer__link:focus-visible,
.footer__logo-link:focus-visible,
.footer__phone:focus-visible {
  outline: solid 1px #2d383f;
  outline-offset: 2px;
}

.footer__phone {
  font-weight: 600;
  color: inherit;
  margin: auto;
  margin-right: 0;
  font-size: 18px;
  grid-column: 3/4;
}
@media (max-width: 1199px) {
  .footer__phone {
    display: none;
  }
}

.footer__social-list {
  list-style: none;
  margin: 0 auto;
  padding: 0;
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}
@media (max-width: 765px) {
  .footer__social-list {
    justify-content: flex-start;
  }
}

.footer__social-item {
  width: 30px;
  height: 30px;
  border: solid 1px rgba(0, 0, 0, 0.3);
  padding: 5px;
}
.footer__social-item:hover {
  border: solid 1px #2d383f;
}
.footer__social-item:active {
  background-color: #0266c1;
  border: solid 1px #0266c1;
  color: #ffffff;
}
.footer__social-item:focus-visible {
  outline: solid 1px #2d383f;
}

.footer__social-item:not(:last-child) {
  margin-right: 15px;
}

.footer__social-link {
  line-height: normal;
  margin: auto;
  color: inherit;
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer__social-link:focus-visible {
  outline: none;
}

.footer__policy-block {
  width: 100%;
  max-width: 1440px;
  display: flex;
  justify-content: center;
}
@media (max-width: 1199px) {
  .footer__policy-block {
    justify-content: center;
  }
}
@media (max-width: 765px) {
  .footer__policy-block {
    justify-content: flex-start;
  }
}

.footer__policy {
  display: inline-block;
  text-align: center;
  font-size: 12px;
  font-weight: 300;
  line-height: 12px;
  max-width: 200px;
  margin: 0 auto;
}
@media (max-width: 765px) {
  .footer__policy {
    text-align: start;
    margin: 0;
  }
}

