.education {
  position: relative;
}

.education__wrapper {
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;

  @include vp-1200 {
    width: 100%;
  }
}

.education__container {
  padding: 30px 90px 0;
  position: relative;

  @include vp-1440 {
    padding: 24px 15px 0;
  }

  @include vp-1200 {
    padding: 24px 25px 0;
  }

  @include vp-768 {
    padding: 25px 15px 0;
  }
}

.education__header {
  font-size: 35px;
  font-weight: 800;
  line-height: 35px;
  position: relative;
  z-index: 10;
  margin: 0;
  margin-bottom: 30px;
  padding-left: 30px;

  @include vp-1200 {
    font-size: 25px;
    line-height: 25px;
    margin-bottom: 20px;
    padding-left: 20px;
  }

  @include vp-768 {
    width: 60%;
    font-size: 22px;
    line-height: 22px;
    margin-bottom: 15px;
    padding-left: 0;
  }
}

.education__header::after {
  content: attr(data-header) "";
  position: absolute;
  bottom: 0;
  left: 0;
  font-size: 55px;
  font-weight: 800;
  line-height: 55px;
  color: $color-default-dark;
  opacity: 0.03;
  padding-left: 30px;

  @include vp-1200 {
    font-size: 40px;
    line-height: 40px;
    padding-left: 20px;
  }

  @include vp-768 {
    font-size: 26px;
    line-height: 26px;
    padding-left: 0;
  }
}

.education__subheader {
  font-size: 21px;
  font-weight: 700;
  line-height: 21px;
  margin: 0;
  margin-bottom: 11px;
  padding-left: 30px;

  @include vp-1200 {
    font-size: 18px;
    line-height: 18px;
    margin-bottom: 0;
    padding-left: 20px;
  }

  @include vp-768 {
    font-size: 16px;
    line-height: 16px;
    margin-bottom: 32px;
    padding-left: 0;
  }
}

.education__slider-buttons {
  position: absolute;
  top: 14px;
  right: 32px;
  display: flex;
  gap: 4px;

  @include vp-1440 {
    top: 0;
    right: 30px;
  }

  @include vp-1200 {
    top: 13px;
    right: 20px;
  }

  @include vp-768 {
    right: 0;
    top: 11px;
  }
}

.education__button {
  width: 37px;
  height: 35px;
  background-color: $color-navy-blue;
  position: static;
  border: none;

  &:hover,
  &:focus-visible {
    background-color: $color-default-white;
    border: solid 1px $color-resolution-blue;
    color: $color-resolution-blue;
    outline: none;
  }

  &:active {
    background-color: $color-resolution-blue;
  }
}

.education__text {
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  margin: 0;
  margin-bottom: 58px;
  width: 70%;
  padding-left: 30px;

  @include vp-1200 {
    width: 80%;
    margin-bottom: 67px;
    padding-left: 20px;
  }

  @include vp-768 {
    font-size: 14px;
    line-height: 20px;
    width: 100%;
    margin-bottom: 50px;
    padding-left: 0;
  }
}

.education__list {
  list-style: none;
  padding: 0;
  margin: 0;

  @include vp-768 {
    padding: 0;
  }
}

.education__content {
  margin-bottom: 21px;
  padding: 30px 30px;

  @include vp-1200 {
    margin-bottom: 14px;
    padding: 32px 20px 30px;
  }

  @include vp-768 {
    padding: 0;
    margin-bottom: 38px;
    box-shadow: 0 0 30px 0 rgba(28, 51, 116, 0.1);
  }
}

.education__item {
  height: auto;
  width: 100%;
  object-fit: cover;
  padding: 19px;
  border: 1px solid $color-default-white;
  background: $color-default-white;
  box-shadow: 0 0 30px 0 rgba(28, 51, 116, 0.1);

  &:focus-visible {
    outline: solid 1px $color-resolution-blue;
  }

  @include vp-1200 {
    height: 100%;
    padding: 14px;
  }

  @include vp-768 {
    height: 100%;
    width: 100%;
    max-width: 100%;
    padding: 20px 20px;
  }
}

.education__image {
  margin-bottom: 16px;
  min-height: 212px;
  width: 100%;
  background-color: #ddb393;

  @include vp-768 {
    margin-bottom: 18px;
  }
}

picture img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.education__instructor-name {
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  margin: 0;
}

.education__instructor-link {
  display: inline-block;
  width: 24px;
  height: 24px;
  border: solid 1px rgba(2, 102, 193, 0.1);
  margin-left: auto;
  margin-top: auto;
  margin-bottom: auto;

  &:hover {
    border: solid 1px $color-default-dark;
  }

  &:active {
    background-color: $color-navy-blue;
    border: solid 1px $color-navy-blue;
    color: $color-default-white;
  }

  &:focus-visible {
    border: solid 1px $color-default-dark;
    outline: none;
  }

  &:disabled {
    color: rgba(2, 102, 193, 0.1);
  }

  @include vp-768 {
    margin-right: 10px;
  }
}

.education__icon-vk {
  margin-left: 3px;
  margin-bottom: 1px;
  color: inherit;
}

.education__instructor-wrapper {
  display: grid;
  grid-template-columns: 112px 1fr;
}

.education__instructor p {
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
  min-height: 64px;
  margin-bottom: 0;

  @include vp-1440 {
    min-height: 108px;
  }

  @include vp-1200 {
    line-height: 18px;
    min-height: 108px;
  }

  @include vp-768 {
    min-height: 72px;
  }
}

.education__slider-container {
  position: relative;
}

.education__buttons-positioning {
  position: relative;
}
